/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import messageCenterData from './data-bb-message-center-http';

const messageCenterDataModuleKey = 'data-bb-message-center-http-ng';

export const messageCenterDataKey = 'data-bb-message-center-http-ng:messageCenterData';

export default ng
  .module(messageCenterDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(messageCenterDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       messageCenterData(config),
      ],
    };
  }])
  .name;



